@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css?family=Oswald&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  font-family: 'Montserrat', sans-serif;
  padding-top: constant(safe-area-inset-top);
  padding-top: env(safe-area-inset-top);
}
html{
  background: rgba(68,36,168,0.10);
}
body{
  padding-top: constant(safe-area-inset-top);
  padding-top: env(safe-area-inset-top);
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.Link {
  color: #000000;
  text-decoration: none;
}

.RegisterLink {
  padding-top: 10px;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.Card {
    border-radius: 16px;
    padding-top: 8px;
    padding-left: 12px;
    padding-right: 7px;
    padding-bottom: 6px;
}
.homescreen{
    text-align: left;
}
.Social {
    position: absolute;
    top: 20px;
    z-index: -3;
}

.Hygiene {
    position: absolute;
    top: 70px;
    z-index: -2;

}

.Behavior {
    position: absolute;
    top: 120px;
    z-index: -1;

}

.PointContainer {
    position: absolute;
    top: 5px;
    left: 60px;
    color: #FFFFFF;
    letter-spacing: 0;
    z-index: 0;
    text-align: left;
}

.Points {
    font-family: 'Oswald';
    font-size: 16px;
    color: #FFFFFF;
    letter-spacing: 0;
}

.Nameline {
    font-family: 'Montserrat';
    font-size: 10px;
    color: #FFFFFF;
    letter-spacing: 0;
    line-height: 12px;
}

.Star{
    position: absolute;
    float: right;
    right: 7px;
    top: 10px;
}



.ProfilePic{
    position: absolute;
    top: 16px;
    right: 51px;
}

.Username {
    font-size: 16px;
    color: #3F306E;
    letter-spacing: 0;
}

.Userpoints {
    font-size: 40px;
    color: #3F306E;
    letter-spacing: 0;
}

.User{
    font-family: 'Oswald';
    text-align: left;
}

.Descriptor {
    font-size: 10px;
    color: #3F306E;
    letter-spacing: 0;
    line-height: 12px;
}
.avatarFrame {
  width: 140px;
  height: 140px;
  border: 3px solid #ffffff;
  border-radius: 40px;
  object-fit: cover;
  display: inline-block;
}
.avatarFrame img{
    width: 134px;
    height: 134px;
    border-radius: 37px;
    object-fit: cover;
    display: inline-block;
}
.custom-file-input::-webkit-file-upload-button {
    display: none;
  }
  .custom-file-input{
    outline: none;
    margin-top: -100px;
    margin-left: 300px;
  }
  .custom-file-input::before {
    content: url(../../static/media/icon_camera.22aa2495.svg);
    display: inline-flex;
    background:#4424A8;
   
/* Sketch doesnt export gradient borders at this point */
/* Sketch doesnt export gradient borders at this point */
box-shadow: 0 0 2px 0 rgba(0,0,0,0.12), 0 2px 3px 0 rgba(0,0,0,0.24);
    border-radius: 28px;
    flex:1 1;
    justify-content: center;
    align-items: center;
    outline: none;
    white-space: nowrap;
    -webkit-user-select: none;
    cursor: pointer;
    
    width: 56px;
    height: 56px;
  }
  .custom-file-input:hover::before {
    border-color: black;
  }
  .custom-file-input:active::before {
    background: -webkit-linear-gradient(top, #e3e3e3, #f9f9f9);
  }
.onboardingScreen{
    background-color:#673ab7;
    height: 100vh;
}
